.pfs {
  display: flex;
  justify-content: space-between;

  label {
    font-family: $sans-serif;
    font-size: 10px;
    font-weight: 800;
    line-height: 1;
    text-transform: uppercase;
    color: var(--primary);
  }

  input {
    font-family: $body-serif;
    font-weight: bold;
    border-bottom: 1px solid #d3ccbc;

    &::placeholder {
      filter: opacity(0.5);
      color: var(--body);
    }

    &:focus {
      border-bottom: 1px solid #644f33;

      &::placeholder {
        color: transparent;
      }
    }
  }

  .organized-play {
    display: grid;
    grid-template-columns: 105px 105px;

    input#pfs-player-number {
      width: 95px;
      text-align: right;
    }

    input#pfs-character-number {
      width: 40px;
    }
  }

  .small-input {
    width: 40px;
  }

  .spacing {
    margin-bottom: 20px;
  }

  .level-bump {
    &.toggle {
      width: 42px;
      height: 16px;
      background: var(--secondary);
      margin: 4px auto;
      position: relative;
      border-radius: 50px;
      box-shadow: inset 0px 1px 1px rgba(0, 0, 0, 0.5), 0px 1px 0px rgba(255, 255, 255, 0.2);

      &::after {
        content: "OFF";
        color: #000;
        position: absolute;
        right: 5px;
        z-index: 0;
        text-shadow: 1px 1px 0px rgba(255, 255, 255, 0.15);
      }

      &::before {
        content: "ON";
        color: white;
        position: absolute;
        left: 6px;
        z-index: 0;
      }

      &::after,
      &::before {
        @include micro;
        top: 4px;
      }

      label {
        display: block;
        width: 18px;
        height: 14px;
        position: absolute;
        top: 1px;
        right: 23px;
        cursor: pointer;
        background: #fcfff4;
        z-index: 1;
        background: linear-gradient(to bottom, #fcfff4 0%, #dfe5d7 40%, #b3bead 100%);
        border-radius: 50px;
        box-shadow: 0 0 0 1px rgba(black, 0.2), 0px 2px 5px 0px rgba(0, 0, 0, 0.3);
        transition: all 0.4s ease;
      }

      input[type="checkbox"] {
        visibility: hidden;

        &:checked + label {
          right: 1px;
        }
      }
    }
  }
}
