ol.saves-data,
ol.difficulty-checks {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  li {
    display: grid;
    grid:
      "label label" auto
      "icon value" auto
      / 1fr 1fr;
    align-items: center;
    justify-items: center;
    padding: 0 8px;
    border-right: 1px solid rgba(black, 0.3);

    .label {
      grid-area: label;

      h4 {
        font-family: $serif;
        font-size: 10px;
        font-weight: 500;
        padding: 0;
        text-align: center;
      }
    }
    svg {
      grid-area: icon;
      margin-right: 4px;
    }
    .data-value {
      grid-area: value;
      padding: 0;
      h2 {
        font-size: 16px;
        line-height: 1.6;
        width: initial;
      }
    }
    &:last-child {
      border-right: none;
    }

    & + li {
      border-left: 1px solid rgba(white, 0.1);
    }
  }
}

ul.sidebar-saves {
  @include p-reset;
  display: grid;
  grid-template-columns: 1fr 1.1fr 1fr;

  .roll-data {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-right: 1px solid rgba(black, 0.1);
    border-left: 1px solid rgba(white, 0.1);

    &:first-child {
      border-left: none;
      padding-right: 4px;
    }

    &:last-child {
      border-right: none;
      padding-left: 4px;
    }

    &:nth-child(2) {
      padding-left: 1px;
    }

    .save-label {
      font-size: 0.75rem;
      font-family: $sans-serif;
      font-weight: 400;
      text-transform: capitalize;
      color: var(--sidebar-label);
      border: none;
      margin-bottom: 2px;
    }

    .save-roll {
      @include flex-center;
      margin: 4px 0;

      h3 {
        margin: 0;
        margin-left: 2px;
      }
    }

    .save-rank,
    .button {
      border-radius: 2px;
      border: 1px solid rgba(black, 0.5);
      padding: 4px;
      cursor: pointer;
    }

    .save-rank {
      background-color: var(--secondary);
      box-shadow: inset 0 0 0 1px rgba(white, 0.1);

      h4 {
        color: white;
        font-weight: 400;
      }
    }

    .button {
      box-shadow: inset 0 0 0 1px rgba(white, 0.4);
      background-color: var(--tertiary);
      color: var(--primary);
      padding: 2px 4px;
    }
  }
}
